*{
  padding: 0;
  margin: 0;
}
body {
  display: flex;
  flex-flow: column wrap;
  background-color: rgb(75, 1, 177);
  align-items: center; 
}

@keyframes FadeInOpacity {
  0% {
    opacity: 0;;
  }
  100% {
    opacity: 1;
  }
}
.bodycontainer{
  animation: 1s ease-out 0s 1 FadeInOpacity;
  display: flex;
  flex-direction: column ;
  align-items: center;
}

nav {
  padding: 20px;
  position: fixed;
  left: 0;
  z-index: 50;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  height: 100vh;
  background-color: rgb(58, 9, 126);
}

.fon {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 0;
  opacity: 0;
  transition: all ease-in 1s;
    display: flex;
  justify-content: center;
  align-items: center;
}

.fon:target {
  opacity: 1;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.fon:target h2 {
  opacity: 0;
  animation: 2s fadeIn forwards .5s;
}

header {
  display: flex;
  height: 100px;
  width: 100%;
  background-color: rgb(44, 0, 106);
  color: white;
  align-items: flex-end;
  justify-content: space-between;
  flex-flow: row wrap;
  position: absolute;
  flex-basis: 30%;
}

header>div {
  position: flex;
  margin: 0 20px 10px 20px;
  padding: 2px;
  background-color: black;
  width: max-content;
  border-radius: 5px;
}

#inicio {
  margin-top: 100px;
}

#leoimg {
  width: 200px;
  border: 3px solid;
  border-radius: 50% 10%;
  transition: border 0.2s linear;
}

#leoimg:hover {
  border: 3px solid rgb(44, 0, 106);
}

a:link {
  color: white;
  font-weight: bolder;
  transition: color 0.2s linear;
  text-decoration: none;
}

a:hover {
  color: grey;
}

a:visited {
  font-weight: bolder;
  transition: color 0.2s linear;
  color: white;
}

@media screen and (max-width: 550px) {

  body {
  background-color: khaki;
}

}
